import React, {Component} from 'react';
import '../Styles/Die.css'

class Die extends Component{
    // constructor(props){
    //     super(props);
    //     this.state = {
    //         num : 
    //     }
    // }
    static defaultProps = {
        num : 1
    }
    render(){
        var dg = ['one', 'two', 'three', 'four', 'five', 'six'];
        return(
            <div className='Die'>
                <i className = {`fas fa-dice-${dg[this.props.num -1]} ${this.props.rolling? "shaking" :
             "" }`}></i> 
            </div>
        );
    }
}

export default Die;