import React, { Component } from 'react';
import Die from './Die';
import '../Styles/RollDice.css';

class RollDice extends Component {
    constructor(props) {
        super(props);
        this.state = {
            rolling: false,
            dice1 : 1,
            dice2 : 1
        }
        this.handleClick = this.handleClick.bind(this);
    }
    handleClick(e) {
        this.setState({
            rolling: true,
            dice1: Math.floor(Math.random()*6 +1),
            dice2: Math.floor(Math.random()*6 +1)      
        });
        setTimeout(() => {this.setState({rolling: false});} , 1000);
    }
    render() {
        return (
            <div className='RollDice'>
                <div className='RollDice_Dices'>
                    <Die num = {this.state.dice1} rolling = {this.state.rolling}/>
                    <Die num = {this.state.dice2} rolling = {this.state.rolling}/>
                </div>
                <button onClick={this.handleClick} disabled = {this.state.rolling}>{this.state.rolling ? "Rolling..." : "Roll Dice!"}</button>
            </div>
        );
    }
}

export default RollDice;